<template>
  <div class="crud">
    <h1>{{titulo}}</h1>
    <div class="container">
        <form  id="formulario">
                <input type="text" id="actividad" class="form-control my-3" v-model="nuevaTarea" v-on:keyup.enter="agregarTarea">
                <button type="submit" class="btn btn-primary" @click="agregarTarea">Agregar</button>
            </form>
            <div  class="mt-4" v-for="(contact, index) in contacts" :key="index">
                <div :class="['alert',contact.estado ? 'alert-success' : 'alert-danger']" role="alert">
                    <div class="d-flex justify-content-between">
                        <div>
                            {{contact.nombre}}
                        </div>
                        <div class="d-flex justify-content-between">
                            <button class="btn btn-success btn-sm" @click="editarTarea(contact.id)">ok</button>
                            <button class="btn btn-danger btn-sm" @click="eliminarTarea(contact.id)">borrar</button>
                            <router-link :to="`/contact/${contact.id}`">Ver mas</router-link>
                        </div>
                    </div>
                </div>
            </div>
        </div>
  </div>
  
</template>

<script>
export default {
  name: 'Crud',
  props: {
    msg: String
  },
  data(){
        return{
            titulo: 'Crud Datos',
            contacts: [],
            nuevaTarea: '',
        };
    },
    methods:{
        async getContacts(){
            const data = await fetch('http://localhost:3000/contact')
            const info = await data.json();
            this.contacts = info;
            console.log(this.contacts);
        },
        agregarTarea: function(){
            this.contacts.push({
                id: this.contacts.length,
                nombre: this.nuevaTarea,
                estado: false,
            });
            this.nuevaTarea = '';
        },
        editarTarea: function(index) {
            console.log('editar' , index );
            this.contacts[index].estado =true;
        },
        eliminarTarea: function(index) {
            this.contacts.splice(index, 1);
            console.log(index);
        }
    },
    created(){
        this.getContacts();
    },
}
</script>

